import { View } from '@tarojs/components'
import { Button, Notify } from '@antmjs/vantui'

export default function Demo() {
  const show = () => {
    Notify.show({
      message: '',
      color: '#ad0000',
      background: '#ffe1e1',
      duration: 10000,
    })
  }

  return (
    <View>
      <Button onClick={show} type="primary">
        
      </Button>
      <Notify id="vanNotify" />
    </View>
  )
}
